}

.header{
    width: 100%;
    max-width: 1250px;
    margin: auto;
    position: relative;
}
.header .navbar{
    max-width: 1250px;
    width: 100%;
    margin: 20px;
    display: -webkit-flex;
    display: flex;
    margin: auto;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 80px;
    -webkit-align-items: center;
            align-items: center;
    position: fixed;
    z-index: 1;
}
.navbar .logo{
    margin: 0px;
    padding: 0px;
}
.navbar .logo img{
    width: 50px;
}
.navbar .navlinks{
    margin: 0px;
    padding: 0%;
}
.navbar .navlinks ul{
    list-style: none;
    margin: 0%;
    padding: 0;
    display: -webkit-flex;
    display: flex;
}
.navbar .navlinks ul li{
    margin: 0px;
    padding: 20px;
    font-size: 16px;
    color: #999;
    font-family: 'Poppins', sans-serif;
