}
.navbar .navlinks ul li:hover{
    color: #e8e8e8;
}
.dropdown-toggle.btn.btn-primary::after{
    display: none;
}
.mobileLinks{
    background: #131313;
    margin: 0%;
    padding: 0%;
}
.mobileLinks .mobileLink{
