}
.welcome{
    text-align: left;
    margin: 20px;
    margin: auto;
    max-width: 1200px;
    padding-top: 80px;
}
.welcome h5{
    color: #999;
    font-size: 20px;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    text-transform: uppercase;
    margin-left: 20px;
    margin-top: 100px;
    letter-spacing: 3px;
}
.welcome h1{
    font-size: 70px;
    margin-left: 20px;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    max-width: 600px;
    color: #fff;
    margin-top: 30px;
    margin-bottom: 10px;
}
.welcome h1 span{
    color: #DA0037;
    text-transform: uppercase;
}
.welcome h1 span::before{
    content: '';
    position: absolute;
    left: 100;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent 0%, #1B232E 5%, #1B232E 100%);
    -webkit-animation: animate 5.5s linear forwards;
            animation: animate 5.5s linear forwards;
}
@-webkit-keyframes animate
{
    0%{
        left: -20%;
    }
    100%{
        left: 100%;
    }
}
@keyframes animate
{
    0%{
        left: -20%;
    }
    100%{
        left: 100%;
    }
}
.welcome p{
    color: #aaa;
    margin-left: 20px;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    letter-spacing: 7px;
    position: relative;
    margin-bottom: 100px;
}
.welcome .row{
    margin: 0%;
    padding: 0%;
}
.welcome .col{
    margin: 0%;
    padding: 0%;
}
.welcome .image{
    margin: auto;
    width: 100%;
    padding: auto;
    text-align: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
.welcome img{
    -webkit-clip-path: circle(56.0% at 51% 50%);
            clip-path: circle(56.0% at 51% 50%);
    max-height: 600px;
    width: 100%;
    width: auto;
    margin: auto;
    margin-left: 100px;
}
@media only screen and (max-width: 780px){
    .welcome img{
        max-height: 500px;
        margin-left: 0px;
    }
    .welcome h1{
        font-size: 50px;
    }
    .welcome .image{
        padding: 0%;
        margin: 0%;
    }
}
@media only screen and (max-width: 700px){
    .welcome .image{
        display: none;
    }
    .welcome img{
        display: none;
    }
    .welcome h5{
        font-size: 18px;
    }
    .welcome h1 span::before{
        position: relative;
    }
}








