/* single work */
.SingleWork{
    margin: 0px;
    width: 100%;
    padding: 0%;
    max-height: 600px
}
.SingleWork img{
    width: 100%;
    height: 500px;
    object-fit: cover;
    position: relative;
}
.SingleWork .aboutWork{
    background: #00000090;
    height: 500px;
    width: 100%;
    position: absolute;
    top: 0;
    text-align: left;
    padding: 20px;
    opacity: 0;
    visibility: hidden;
    cursor: pointer;
}
.SingleWork .aboutWork h2{
    color: #dfdfdf;
    font-size: 32px;
    font-family: 'Poppins', sans-serif;
    font-weight: 550;
    margin-top: 320px;
    margin-left: 20px;
    margin-bottom: 10px;
}
.SingleWork .aboutWork p{
    color: #aaa;
    font-family: 'Poppins', sans-serif;
    font-weight: 550;
    text-transform: uppercase;
    margin: 20px;
}
.SingleWork:hover > .aboutWork{
    visibility: visible;
    opacity: 1;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: translate3d(0%, 0, 75px);
            transform: translate3d(0%, 0, 75px);
    transition: 0.6s;
}
